<template>
  <div>
    <div class="wallet-detail">
      <div class="wallet-type-header">
        <div class="text-color-gredient" style="font-size: 25px">{{LockWalletTypeLabel.toUpperCase()}}</div>
        <div style="text-align: right">
          <div style="color: #929292">
            <img style="width: 25px; vertical-align: sub" alt="kookl-coin" src="@/assets/images/kookl-coin.png"> Total Lock
          </div>
          <div class="text-color-gradient font-bold text-shadow">{{numberFormat(walletBalance)}} KOOKL</div>
        </div>
      </div>
      <div class="withdraw-body">
        <div style="color: #929292; font-size: 18px; font-weight: bold">Available Withdraw</div>
        <div class="text-color-gradient font-bold text-shadow" style="font-size: 35px">{{numberFormat(availableWithdraw)}} KOOKL</div>
        <div>
          <el-button type="primary" class="btn-withdraw" size="mini"
                     :class="{'btn-disable': parseInt(availableWithdraw) === 0}"
                     :disabled="parseInt(availableWithdraw) === 0"
                     @click="onOpenPopupWithdrawButtonClick(availableWithdraw)">WITHDRAW
          </el-button>
        </div>
      </div>
      <div style="margin-bottom: 20px">
        <div v-for="(item, index) of walletReleaseRules" :key="`release-rule-${index}`" class="release-rules">
          <div style="color: #929292">{{item.unlockDate}}</div>
          <div style="color: var(--primary-color)">{{item.percentReleaseToken}}%</div>
        </div>
        <div style="color: #929292; text-align: right; font-size: 12px; margin: 0 30px">
          Your total supply: {{numberFormat(totalSaleAmount)}} KOOKL
        </div>
      </div>
      <div class="transaction-history">
        <div style="text-align: center; padding: 20px 0 5px 0; cursor: pointer"
             @click="isOpenTransactionHistory = !isOpenTransactionHistory">Transaction History
          <i v-if="!isOpenTransactionHistory" class="el-icon-arrow-down font-bold"></i>
          <i v-else class="el-icon-arrow-up font-bold"></i>
        </div>
        <div v-if="isOpenTransactionHistory" style="margin: 0 30px">
          <div v-for="(item, index) of transactionList" :key="`history-${index}`"
               style="display: flex; justify-content: space-between; cursor: pointer; margin: 10px 0">
            <div>
              <div :class="{
                    'withdraw-color': item.action === 'withdraw',
                    'deposit-color': item.action === 'deposit'
                  }">{{item.action.toUpperCase()}}</div>
              <div style="font-size: 13px; color: #929292">{{item.createdAt}}</div>
            </div>
            <div>
              <span v-if="item.action === 'withdraw'" class="withdraw-color">
                <i> - </i>{{numberFormat(item.amount)}}
              </span>
              <span v-if="item.action === 'deposit'" class="deposit-color">
                <i> + </i>{{numberFormat(item.amount)}}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisible" width="500px">
      <div style="color: #929292; position: relative">
        <img style="width: 80px; position: absolute; top: -85px; left: 200px;" alt="kookl-coin" src="@/assets/images/kookl-coin.png">
        <div style="text-align: center">
          <div style="color: var(--primary-color); font-size: 20px;">WITHDRAW</div>
          <div style="font-size: 14px">{{LockWalletTypeLabel.toUpperCase()}} WALLET</div>
        </div>
        <div style="padding: 20px 20px 0 20px;">
          <div>{{todayReleaseRules.date}} = {{todayReleaseRules.percent}}% of {{numberFormat(todayReleaseRules.totalSupply)}} KOOKL</div>
        </div>
        <div style="padding: 0 20px 20px 20px;">
          <div class="block" style="position: relative">
            <el-slider v-model="withdrawPercent" @change="onWithdrawPercentChange"></el-slider>
            <el-input v-model="totalWithdraw" style="color: var(--primary-color)" @input="onTotalWithdrawChange"></el-input>
            <div style="position: absolute; right: 5px; bottom: 7px; font-size: 18px; color: var(--primary-color); opacity: 0.5;">KOOKL</div>
          </div>
          <div style="width: 100%; color: red" v-if="totalWithdraw > parseFloat(availableWithdraw)">
            maximum withdraw is {{numberFormat(availableWithdraw)}} KOOKL
          </div>
          <div style="text-align: right; margin-top: 10px;">
            Wallet Balance: <span style="color: var(--primary-color)">{{numberFormat(kookleBalanceWallet)}} KOOKL</span>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="default" @click="dialogVisible = false" style="border-radius: 30px">CANCEL</el-button>
        <el-button type="primary"
                   style="border-radius: 30px"
                   @click="onConfirmWithdrawButtonClick"
                   :class="{'btn-disable': isDisableConfirmWithdrawButton()}"
                   :disabled="isDisableConfirmWithdrawButton()">CONFIRM
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Web3 from 'web3'
import dateFormat from 'dateformat'

import { get } from 'lodash'
import { numberFormat } from '@/helpers'

export default {
  name: 'WalletComponent',
  props: {
    kookleTokenInstance: null,
    walletInstance: null,
    currentAddress: null,
    walletType: String
  },
  computed: {
    kookleInstance () {
      return this.kookleTokenInstance
    },
    lockWalletInstance () {
      return this.walletInstance
    },
    walletAddress () {
      return this.currentAddress
    },
    LockWalletTypeLabel () {
      return this.walletType
    }
  },
  watch: {
    kookleTokenInstance: async function () {
      const kookleTokenBalance = await this.kookleInstance.methods.balanceOf(this.walletAddress).call()
      this.kookleBalanceWallet = this.web3.utils.fromWei(kookleTokenBalance)
    },
    walletInstance: async function () {
      try {
        await this.prepareWalletData()
      } catch (err) {
        console.log(err.message)
      }
    }
  },
  data () {
    return {
      numberFormat,
      eth: null,
      web3: null,
      kookleBalanceWallet: 0,
      walletBalance: 0,
      availableWithdraw: 0,
      walletReleaseRules: [],
      transactionList: [],
      totalSaleAmount: 0,
      isOpenTransactionHistory: false,
      withdrawPercent: 100,
      totalWithdraw: 0.0,
      withdrawType: 'seed',
      walletLabel: 'Seed',
      dialogVisible: false,
      isConfirmButtonClick: false,
      todayReleaseRules: {
        date: '',
        percent: '',
        totalSupply: ''
      }
    }
  },
  async created () {
    this.web3 = new Web3(window.ethereum)
  },
  methods: {
    prepareWalletData: async function () {
      const result = await Promise.all([
        this.lockWalletInstance.methods.balanceOf(this.walletAddress).call(),
        this.lockWalletInstance.methods.availableWithdrawOf(this.walletAddress).call(),
        this.lockWalletInstance.methods.releaseRules().call(),
        this.lockWalletInstance.methods.transactionOf(this.walletAddress).call(),
        this.lockWalletInstance.methods.totalPrivateSaleAmount(this.walletAddress).call()
      ])

      const balance = result[0]
      const availableWithdraw = result[1]
      const walletReleaseRules = result[2]
      const transactionList = result[3]
      const totalSeedSaleAmount = result[4]

      this.walletBalance = this.web3.utils.fromWei(balance)
      this.availableWithdraw = this.web3.utils.fromWei(availableWithdraw)
      this.totalSaleAmount = this.web3.utils.fromWei(totalSeedSaleAmount)

      this.walletReleaseRules = this._prepareReleaseRules(walletReleaseRules)
      this.transactionList = this._prepareTransactionList(transactionList)
    },
    onOpenPopupWithdrawButtonClick: async function (withdrawAvailable) {
      const todayDate = new Date()

      this.walletReleaseRules.forEach(item => {
        if (todayDate > item.date) {
          this.todayReleaseRules.date = get(item, 'unlockDate', '')
          this.todayReleaseRules.percent = get(item, 'percentReleaseToken', '')
        }
      })

      this.todayReleaseRules.totalSupply = this.totalSeedSaleAmount
      this.withdrawPercent = 100
      this.totalWithdraw = withdrawAvailable
      this.dialogVisible = true
    },
    onWithdrawPercentChange: function () {
      this.totalWithdraw = Number(this.availableWithdraw) * (this.withdrawPercent / 100)
    },
    onTotalWithdrawChange: function () {
      this.totalWithdraw = this.totalWithdraw.replace(/[^0-9.]+/g, '')
      this.withdrawPercent = (this.totalWithdraw / Number(this.availableWithdraw)) * 100
    },
    getMaximumWithdraw: function () {
      return this.availableWithdraw
    },
    onConfirmWithdrawButtonClick: async function () {
      this.isConfirmButtonClick = true

      const totalWithdraw = this.web3.utils.toWei((this.totalWithdraw).toString())
      await this.lockWalletInstance.methods.withdraw(totalWithdraw).send({
        from: this.currentAddress
      })
      const transactionList = await this.lockWalletInstance.methods.transactionOf(this.walletAddress).call()

      this.walletBalance = parseFloat(this.walletBalance) - this.totalWithdraw
      this.availableWithdraw = parseFloat(this.availableWithdraw) - this.totalWithdraw
      this.transactionList = this._prepareTransactionList(transactionList)

      this.dialogVisible = false
      this.isConfirmButtonClick = false
    },
    isDisableConfirmWithdrawButton: function () {
      const isOverMaximumWithdraw = (this.totalWithdraw > parseFloat(this.getMaximumWithdraw()))
      const isEmptyTotalWithdraw = (this.totalWithdraw === 0 || this.totalWithdraw === '')

      return (isOverMaximumWithdraw || isEmptyTotalWithdraw || this.isConfirmButtonClick)
    },
    _prepareReleaseRules: function (releaseRules) {
      const result = releaseRules.map(item => {
        const unlockDate = new Date(parseInt(item.unlockTime) * 1000)
        return {
          date: unlockDate,
          unlockDate: dateFormat(unlockDate, 'ddd, d mmmm yyyy HH:MM'),
          percentReleaseToken: item.percentReleaseToken
        }
      })

      return result.reverse()
    },
    _prepareTransactionList: function (transactionList) {
      const result = transactionList.map(item => {
        const createdAt = new Date(parseInt(item.created) * 1000)
        const amount = this.web3.utils.fromWei(item.amount)
        return {
          action: item.action,
          amount: Number(amount).toFixed(2),
          createdAt: dateFormat(createdAt, 'ddd, d mmmm yyyy HH:MM')
        }
      })

      return result.reverse()
    }
  }
}
</script>

