    border-bottom: 1px solid #415254;
    padding: 10px 0 15px 0;
  }

  .text-shadow {
    text-shadow: 0 0 10px #70e2d380;
  }

  .withdraw-body {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 20px 30px;
  }

  .btn-withdraw {
    font-size: 17px;
    padding: 15px 25px;
    border-radius: 30px;
  }

  .withdraw-body > div {
    margin: 5px 0;
