const state = {
  walletAddress: ''
}

const getters = {}

const actions = {
  setWalletAddress (context, payLoad) {
    context.commit('setWalletAddress', payLoad)
  }
}

const mutations = {
  setWalletAddress (state, payLoad) {
    state.walletAddress = payLoad
  }
}

export default {
  name: 'wallet',
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
