import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const messages = {
  en: {
    hello: 'Global: Hello'
  },
  th: {
    hello: 'Global: สวัสดี'
  }
}

const i18n = new VueI18n({
  locale: 'en', // set locale
  fallbackLocale: 'en',
  silentFallbackWarn: true,
  messages: messages // set locale messages
})

export default i18n
