import Vue from 'vue'
import Router from 'vue-router'
import LockWallet from '@/views/LockWallet/LockWallet'
import SubscribeOracleRoute from '@/views/SubscribeOracle/router'
import DemoRoute from '@/views/Demo/router'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/oracle'
    },
    {
      path: '/oracle',
      component: () => import(/* webpackChunkName: "subscribe-oracle" */ '@/views/SubscribeOracle/Index.vue'),
      children: SubscribeOracleRoute
    },
    {
      path: '/demo',
      component: () => import(/* webpackChunkName: "subscribe-oracle" */ '@/views/Demo/Index.vue'),
      children: DemoRoute
    },
    {
      path: '/wallet',
      name: 'LockWallet',
      component: LockWallet
    }
  ]
})
