<template>
  <div id="wallet" class="content">
    <div style="margin-left: 10px; display: flex; justify-content: space-between">
      <h1 class="h1-label text-color-gredient font-normal"
          style="text-align: left">
        Wallet
      </h1>
      <el-button v-if="!isWalletConnected" style="font-size: 23px; margin: 25px 0" type="primary" @click="connectWallet">
        <i class="el-icon-s-finance"></i> Connect Wallet
      </el-button>
      <el-button v-else style="font-size: 23px; margin: 25px 0" type="default">
        <i class="el-icon-s-finance"></i> {{getShortWalletAddress()}}
      </el-button>
    </div>
    <div v-if="isWalletConnected">
      <div class="wallet-content">
        <wallet-component
          :kookle-token-instance="kookleTokenInstance"
          :wallet-instance="seedWalletInstance"
          :current-address="currentAddress"
          :wallet-type="WALLET_TYPE_SEED"
        />
        <wallet-component
          :kookle-token-instance="kookleTokenInstance"
          :wallet-instance="privateWalletInstance"
          :current-address="currentAddress"
          :wallet-type="WALLET_TYPE_PRIVATE"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import { numberFormat } from '@/helpers'

import seedWalletAbi from '@/abi/SeedWallet'
import privateWalletAbi from '@/abi/PrivateWallet'
import kookleTokenAbi from '@/abi/KookleToken.json'
import WalletComponent from './components/WalletComponent'

const seedWalletAddress = process.env.VUE_APP_CONTRACT_SEED_WALLET
const privateWalletAddress = process.env.VUE_APP_CONTRACT_PRIVATE_WALLET
const kookleTokenAddress = process.env.VUE_APP_CONTRACT_KOOKLE_TOKEN

const WALLET_TYPE_SEED = 'seed'
const WALLET_TYPE_PRIVATE = 'private'

export default {
  name: 'LockWallet',
  components: {
    WalletComponent
  },
  data () {
    return {
      seedWalletAddress,
      privateWalletAddress,
      numberFormat,
      WALLET_TYPE_SEED,
      WALLET_TYPE_PRIVATE,
      web3: null,
      eth: null,
      isWalletConnected: false,
      currentAddress: '',
      kookleTokenInstance: null,
      privateWalletInstance: null,
      seedWalletInstance: null
    }
  },
  computed: {
  },
  async created () {
    this.eth = window.ethereum
    this.web3 = new Web3(this.eth)

    if (!this.isWalletConnected) {
      await this.connectWallet()
    }
  },
  methods: {
    connectWallet: async function () {
      this.isWalletConnected = await this.ethEnabled()
      this.accountAddressList = await window.web3.eth.getAccounts()
      this.currentAddress = this.accountAddressList[0]

      try {
        this.kookleTokenInstance = new this.web3.eth.Contract(kookleTokenAbi, kookleTokenAddress)
        this.seedWalletInstance = new this.web3.eth.Contract(seedWalletAbi, seedWalletAddress)
        this.privateWalletInstance = new this.web3.eth.Contract(privateWalletAbi, privateWalletAddress)
      } catch (err) {
        console.log(err.message)
      }
    },
    ethEnabled: async function () {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum)
        await window.ethereum.enable()

        return true
      }

      return false
    },
    getShortWalletAddress: function () {
      return `${this.currentAddress.substring(0, 4)}...${this.currentAddress.substring(this.currentAddress.length - 4, this.currentAddress.length)}`
    }
  }
}
</script>

<style scoped>
  .content {
    position: relative;
    text-align: left;
    padding: 60px 20px;
    max-width: 950px;
    margin: 0 auto;
  }

  .wallet-content {
    display: flex;
    justify-content: space-between
  }

  @media screen and (max-width:768px) {
    .wallet-content {
      flex-direction: column;
    }
  }
</style>
